import React from 'react';

class Clock extends React.Component{
    constructor(props){
        super(props);
    }
    render(){
        let timeMinutes = this.props.minutes < 10 ? "0" + this.props.minutes : this.props.minutes;
        let timeSeconds = this.props.seconds < 10 ? "0" + this.props.seconds : this.props.seconds;
       return<div className="containerClock">
           <h3 id="timer-label">{this.props.clockText}</h3>
           <button id="start_stop" onClick={this.props.handleStartStop}>Start/Stop</button>
           <button id="reset" onClick={this.props.handleReset}>Reset</button>
           <div id="time-left" className="time">{timeMinutes}:{timeSeconds}</div>
       </div>
    }
}

export default Clock;